<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="deleteOne">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="counter" name="count" />

    <button type="button" aria-label="Добавить один товар" @click.prevent="addOne">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: 'ProductCounter',
  props: ['value'],
  methods: {
    updateValue(num) {
      this.$emit('update:value', num);
    },
    deleteOne() {
      if (this.counter <= 0) return;
      this.updateValue(--this.counter);
    },
    addOne() {
      this.updateValue(++this.counter);
    },
  },
  computed: {
    counter: {
      get() {
        return +this.value;
      },
      set(num) {
        this.updateValue(num);
      },
    },
  },
};
</script>
