<template>
  <ul class="catalog__list">
    <ProductItem
      v-for="product in products"
      :key="product.id"
      :product="product"
      @update:color="updateColor"
    ></ProductItem>
  </ul>
</template>

<script>
import ProductItem from './ProductItem.vue';

export default {
  name: 'ProductsList',
  components: { ProductItem },
  props: ['products'],
  methods: {
    updateColor($event) {
      this.$emit('update:color', $event);
    },
  },
};
</script>
