<template>
  <main class="content container">
    <BaseLoader v-if="orderIsSending" />
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> 3 товара </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="sendOrder">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormInput
              :title="'ФИО'"
              :error="formErrors.name"
              :placeholder="'Введите ваше полное имя'"
              v-model="formData.name"
            />
            <BaseFormInput
              :title="'Адрес доставки'"
              :error="formErrors.address"
              :placeholder="'Введите ваш адрес'"
              v-model="formData.address"
            />
            <BaseFormInput
              :title="'Телефон'"
              :error="formErrors.phone"
              :placeholder="'Введите ваш телефон'"
              v-model="formData.phone"
            />
            <BaseFormInput
              :title="'Email'"
              :error="formErrors.email"
              :placeholder="'Введите ваш Email'"
              v-model="formData.email"
            />
            <BaseFormText
              :title="'Комментарий к заказу'"
              :error="formErrors.comment"
              v-model="formData.comment"
              :placeholder="'Ваши пожелания'"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card" />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash" />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <OrderProducts :products="products" />

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ productsCount }}</b> товара на сумму <b>{{ total | numberFormat }} ₽</b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">Оформить заказ</button>
        </div>
        <div v-if="formErrorMessage" class="cart__error form__error-block">
          <h4>Заявка не отправлена!</h4>
          <p>{{ formErrorMessage }}</p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormField from '@/components/BaseFormField.vue';
import BaseFormInput from '@/components/BaseFormInput.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseLoader from '@/components/BaseLoader.vue';
import OrderProducts from '@/components/OrderProducts.vue';
import cartProductsMixin from '@/mixins/cartProductsMixin';
import { mapActions, mapMutations } from 'vuex';

export default {
  name: 'OrderPage',
  components: { BaseFormField, BaseFormInput, BaseFormText, OrderProducts, BaseLoader },
  data() {
    return {
      formData: {},
      formErrors: {},
      formErrorMessage: '',
      orderIsSending: false,
    };
  },
  methods: {
    ...mapActions({ placeOrder: 'placeOrder' }),
    ...mapMutations({ resetCart: 'resetCart', updateOrderInfo: 'updateOrderInfo' }),
    async sendOrder() {
      this.orderIsSending = true;
      this.formErrors = {};
      const { response, payload } = await this.placeOrder(this.formData);

      if (!response.ok) {
        this.formErrors = payload.error.request || {};
        this.formErrorMessage = payload.error.message;
      } else {
        this.formData = {};
        this.resetCart();
        this.updateOrderInfo(payload);
        this.$router.push({ name: 'order-info', params: { id: payload.id } });
      }
      this.orderIsSending = false;
    },
  },
  mixins: [cartProductsMixin],
};
</script>
