<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="product.img" width="120" height="120" :alt="product.title" />
    </div>
    <h3 class="product__title">
      {{ product.title }}
    </h3>

    <span class="product__code"> Артикул: {{ product.id }} </span>

    <ProductCounter :value="amount" @update:value="updateAmount" />

    <b class="product__price"> {{ (amount * product.price) | numberFormat }} ₽ </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import ProductCounter from '@/components/ProductCounter';

export default {
  name: 'CartItem',
  components: { ProductCounter },
  props: ['product'],
  filters: {
    numberFormat,
  },

  methods: {
    ...mapActions(['deleteProduct', 'updateProductAmount']),
    updateAmount($event) {
      this.amount = $event;
    },
  },
  computed: {
    amount: {
      get() {
        return this.product.amount;
      },
      set(value) {
        if (value < 0) return;
        if (value === 0) {
          this.deleteProduct(this.product.id);
        } else {
          this.updateProductAmount({ id: this.product.id, amount: value });
        }
      },
    },
  },
};
</script>
