import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccsessKey: null,
    cartProductsData: [],
    cartIsLoading: false,
    orderInfo: null,
  },
  getters: {
    cartProductDetails(state) {
      return state.cartProductsData.map((item) => {
        return {
          id: item.product.id,
          title: item.product.title,
          img: item.product.image.file.url,
          amount: +item.quantity,
          price: +item.price,
        };
      });
    },
    cartTotal(state, getters) {
      return getters.cartProductDetails.reduce((acc, curr) => acc + curr.amount * curr.price, 0);
    },
    getProductById: (state) => (id) => {
      return state.cartProducts.find((product) => product.productId === id);
    },
    cartIsLoading(state) {
      return state.cartIsLoading;
    },
    cartProductsCount(state, getters) {
      return getters.cartProductDetails.reduce((acc, curr) => acc + curr.amount, 0);
    },
    getOrderInfo(state) {
      return state.orderInfo;
    },
  },
  mutations: {
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateProductAmount(state, payload) {
      state.cartProducts.forEach((item) => {
        if (item.productId === payload.id) {
          item.amount = payload.amount;
        }
      });
    },

    updateUserKey(state, userKey) {
      state.userAccsessKey = userKey;
    },
    updateCartProductsData(state, data) {
      state.cartProductsData = data;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
    loadingToggle(state, flag) {
      state.cartIsLoading = flag;
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
  },
  actions: {
    async loadCart(context) {
      const url = new URL('https://vue-study.skillbox.cc/api/baskets');
      url.searchParams.append('userAccessKey', context.state.userAccsessKey);
      context.commit('loadingToggle', true);
      try {
        const res = await fetch(url);
        if (!res.ok) throw new Error('Could not be able to load cart');
        const data = await res.json();
        if (!context.state.userAccsessKey) {
          const key = data.user.accessKey;
          localStorage.setItem('userAccsessKey', key);
          context.commit('updateUserKey', key);
        }
        context.commit('updateCartProductsData', data.items);
        context.commit('syncCartProducts');
        context.commit('loadingToggle', false);
      } catch (e) {
        console.warn(e);
        context.commit('loadingToggle', false);
      }
    },
    async addProductToCart(context, { productId, amount }) {
      const url = new URL('https://vue-study.skillbox.cc/api/baskets/products');
      url.searchParams.append('userAccessKey', context.state.userAccsessKey);
      try {
        const res = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ productId, quantity: amount }),
        });
        if (!res.ok) throw new Error('Something went wrong');
        const data = await res.json();
        context.commit('updateCartProductsData', data.items);
        context.commit('syncCartProducts');
      } catch (e) {
        console.warn(e);
      }
    },
    async updateProductAmount(context, payload) {
      context.commit('updateProductAmount', payload);
      const { id, amount } = payload;
      const url = new URL('https://vue-study.skillbox.cc/api/baskets/products');
      url.searchParams.append('userAccessKey', context.state.userAccsessKey);
      try {
        const res = await fetch(url, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ productId: id, quantity: amount }),
        });
        if (!res.ok) throw new Error('Something went wrong');
        const data = await res.json();
        context.commit('updateCartProductsData', data.items);
      } catch (e) {
        context.commit('syncCartProducts');
        console.warn(e);
      }
    },
    async deleteProduct(context, id) {
      const url = new URL('https://vue-study.skillbox.cc/api/baskets/products');
      url.searchParams.append('userAccessKey', context.state.userAccsessKey);
      try {
        const res = await fetch(url, {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ productId: id }),
        });
        if (!res.ok) throw new Error('Something went wrong');
        const data = await res.json();
        context.commit('updateCartProductsData', data.items);
        context.commit('syncCartProducts');
      } catch (e) {
        console.warn(e);
      }
    },
    async placeOrder(context, formData) {
      const url = new URL('https://vue-study.skillbox.cc/api/orders');
      url.searchParams.append('userAccessKey', context.state.userAccsessKey);
      try {
        const res = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify(formData),
        });
        const data = await res.json();
        return { response: res, payload: data };
      } catch (e) {
        console.log(e);
      }
    },
    async loadOrder(context, orderId) {
      console.log('ID', orderId);
      const url = new URL(`https://vue-study.skillbox.cc/api/baskets/${orderId}`);
      url.searchParams.append('userAccessKey', context.state.userAccsessKey);
      try {
        const res = await fetch(url);
        if (!res.ok) throw new Error('Could not be able to load order');
        const data = await res.json();
        context.commit('updateOrderInfo', data);
      } catch (e) {
        console.warn(e);
      }
    },
  },
  modules: {},
});
