<script>
import numberFormat from '@/helpers/numberFormat';
import ProductCounter from '@/components/ProductCounter';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'ProductPage',
  components: { ProductCounter },
  data() {
    return {
      productAmount: 0,
      productData: null,
      isLoading: false,
      isError: false,
      isTransacting: false,
      transactionDone: false,
    };
  },

  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      this.isTransacting = true;
      const newProduct = { productId: this.product.id, amount: +this.productAmount };
      this.addProductToCart(newProduct).then(() => {
        this.isTransacting = false;
        this.transactionDone = true;
        setTimeout(() => {
          this.transactionDone = false;
        }, 3000);
      });
    },
    updateProducts($event) {
      this.productAmount = $event;
    },
    async loadProduct() {
      const url = new URL(`https://vue-study.skillbox.cc/api/products/${this.$route.params.id}`);
      this.isLoading = true;
      try {
        const res = await fetch(url);
        if (!res.ok) throw new Error('Something wrong');
        const data = await res.json();
        this.productData = data;
        this.isLoading = false;
      } catch (e) {
        console.warn(e);
        this.isError = true;
        this.isLoading = false;
      }
    },
  },

  beforeMount() {
    this.productAmount = this.productAmountInCart;
  },
  computed: {
    ...mapGetters(['getProductById']),
    productId() {
      return parseInt(this.$route.params.id);
    },
    productAmountInCart() {
      const product = this.getProductById(this.productId);
      if (!product) return 0;
      return product.amount;
    },
    product() {
      return this.productData ? this.productData : null;
    },
    priceFormated() {
      return numberFormat(this.product.price);
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
