<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <ProductsFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color-id.sync="filterColorId"
      />
      <section class="catalog">
        <div v-if="isLoading">Ищем товары...</div>
        <div v-else-if="isError">Произошла ошибка...</div>
        <ProductsLists v-else :products="products" @update:color="selectProductColor" />
        <BasePagination :page.sync="page" :perPage="productsPerPage" :count="productsCount" />
      </section>
    </div>
  </main>
</template>

<script>
import ProductsLists from '@/components/ProductsList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductsFilter from '@/components/ProductsFilter.vue';

export default {
  name: 'MainPage',
  components: { ProductsLists, BasePagination, ProductsFilter },

  data() {
    return {
      productsData: null,
      productsPerPage: 6,
      page: 1,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      isLoading: false,
      isError: false,
    };
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  methods: {
    selectProductColor({ id, value }) {
      this.products.forEach((product) => {
        if (product.id === id) {
          product.selectedColorId = value;
        }
      });
    },
    async loadProducts() {
      const url = new URL('https://vue-study.skillbox.cc/api/products');
      url.search = new URLSearchParams({
        page: this.page,
        limit: this.productsPerPage,
        categoryId: this.filterCategoryId,
        minPrice: this.filterPriceFrom,
        maxPrice: this.filterPriceTo,
        colorId: this.filterColorId,
      });
      this.isLoading = true;
      try {
        const res = await fetch(url);
        if (!res.ok) throw new Error('Something went wrong');
        const data = await res.json();
        this.productsData = data;
        this.isLoading = false;
      } catch (e) {
        console.warn(e);
        this.isLoading = false;
        this.isError = true;
      }
    },
  },
  created() {
    this.loadProducts();
  },
  computed: {
    filteredProducts() {
      let filteredProducts = products;

      if (this.filterPriceFrom) {
        filteredProducts = filteredProducts.filter(
          (product) => product.price >= this.filterPriceFrom
        );
      }
      if (this.filterPriceTo) {
        filteredProducts = filteredProducts.filter(
          (product) => product.price <= this.filterPriceTo
        );
      }
      if (this.filterCategoryId) {
        filteredProducts = filteredProducts.filter(
          (product) => product.categoryId === this.filterCategoryId
        );
      }
      if (this.filterColorId) {
        filteredProducts = filteredProducts.filter((product) =>
          product.colorsId.includes(this.filterColorId)
        );
      }
      return filteredProducts;
    },
    products() {
      if (!this.productsData) return [];
      return this.productsData.items.map((product) => {
        return {
          ...product,
          img: product.image.file.url,
        };
      });
    },
    productsCount() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
};
</script>
